.banner {
    background-size: cover;
    height: 448px;
    color: white;
    position: relative;
}

.content {
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;
}

.title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.button {
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding: 0.5rem 2rem;
    background-color: rgba(51, 51, 51, .5);
    cursor: pointer;
    margin-right: 1rem;
    transition: background-color 0.3s ease;
}

.button:hover {
    color: black;
    background-color: #e6e6e6;
}

.description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
}

.fade_bottom {
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, .61), #111);
}

/* Responsive adjustments */
@media (max-width: 1024px) {
    .banner {
        height: 400px;
    }
    .content {
        padding-top: 120px;
        padding-left: 10px;
    }
    .title {
        font-size: 2.5rem;
    }
    .description {
        width: 35rem;
        font-size: 0.9rem;
        max-width: 300px;
    }
}

@media (max-width: 768px) {
    .banner {
        height: 350px;
    }
    .content {
        padding-top: 100px;
        padding-left: 8px;
    }
    .title {
        font-size: 2rem;
    }
    .description {
        width: 30rem;
        font-size: 0.8rem;
        max-width: 280px;
    }
    .button {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }
}

@media (max-width: 480px) {
    .banner {
        height: 300px;
    }
    .content {
        padding-top: 80px;
        padding-left: 5px;
    }
    .title {
        font-size: 1.5rem;
    }
    .description {
        width: 100%;
        font-size: 0.8rem;
        max-width: 250px;
    }
    .button {
        padding-left: 1rem;
        padding-right: 1rem;
        font-size: 0.9rem;
    }
}
